// @flow

import styled from 'styled-components/macro';
import Logo from '../logo';
import Navigation from '../navigation';
import {hostname} from '../../config';
import {colors, spacing, page} from '../../config/styles';
import Link from 'next/link';

export default function Header(props: Object) {
    return (
        <GridCell>
            <Container {...props}>
                <Inner>
                    <Link href="/" passHref>
                        <Logo as="a" title={`${hostname} homepage`} />
                    </Link>
                    <Navigation />
                </Inner>
            </Container>
        </GridCell>
    );
}

// A fixed header needs to be rendered
// in a GridCell (or any other relative block-level element)
// for CSS grid to fill up the space it should take.
const GridCell = styled.div`
    position: relative;
    height: ${page.header.height};
`;

const Container = styled.header`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: ${page.header.height};
    ${colors.withVar('white', 'background-color')};
    box-shadow: 0px 0px ${spacing.small} rgba(0, 0, 0, 0.15);
    z-index: 100;
`;

const Inner = styled.div`
    max-width: ${page.width};
    padding: 0 0 0 ${spacing.medium};
    margin: 0 auto;
    height: calc(100% - 2px);
    display: grid;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
`;
